"use client";
import { useState } from "react";
import Image from "next/image";
import { HomeNavButton, MyPostNavButton } from "@/data/appTexts";

export default function Header() {
  const [activeButton, setActiveButton] = useState<string | null>(null);
  return (
    <div className="flex justify-between items-center h-16 text-lg sm:text-xl fixed top-0 left-0 w-full z-1 bg-white shadow">
      <Image
        src="/PostimLogo.png"
        alt="Logo"
        width={100}
        height={100}
        className="pl-6"
      />
      <div className="flex gap-4 pr-9">
        <button
          onClick={() => setActiveButton("Home")}
          className={`${
            activeButton === "Home" ? "text-[#2463eb]" : "text-black"
          } cursor-pointer`}
        >
          {HomeNavButton}
        </button>
        <button
          onClick={() => setActiveButton("Posts")}
          className={`${
            activeButton === "Posts" ? "text-[#2463eb]" : "text-black"
          } cursor-pointer`}
        >
          {MyPostNavButton}
        </button>
      </div>
    </div>
  );
}
